body {
    margin-top: 100px;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
}

.toast {
    height: 'fit-content';
    width: 300px;
    float: left;
}

.wrapper {
    height: 'fit-content';
    width: 300px;
    height: auto;
    background-color: rgba(7 7 7 / 0.5);
    float: left;
}

.main {
    margin: 12px 16px 12px 56px;
    position: relative;
}

.main::before {
    font-size: 24px;
    top: calc(50% - 12px);
    left: -40px;
    line-height: 24px;
    position: absolute;
}

.success-icon::before {
    font-family: "Font Awesome 5 Free";
    content: "\f058";
    color: #89da46;
}

.success {
    background-color: rgba(7 7 7 / 0.5);
    color: #fff;
    padding: 5px 5px 5px 5px;
}

.info-icon::before {
    font-family: "Font Awesome 5 Free";
    content: "\f05a";
    color: #418dff;
    font-weight: 600;
    font-size : 1.7rem;
}

.info {
    background-color: rgba(7 7 7 / 0.5);
    color: #fff;
    padding: 5px 5px 5px 5px;
}


.warning-icon::before {
    font-family: "Font Awesome 5 Free";
    content: "\f06a";
    color: #ffa800;
    font-weight: 600;
    font-size : 1.7rem;
}

.warning {
    background-color: rgba(7 7 7 / 0.5);
    color: #fff;
    padding: 5px 5px 5px 5px;
}

.error-icon::before {
    font-family: "Font Awesome 5 Free";
    content: "\f06a";
    color: #ff392d;
    font-weight: 600;
    font-size : 1.7rem;
}

.error {
    background-color: rgba(7 7 7 / 0.5);
    color: #fff;
    padding: 5px 5px 5px 5px;
}
